@keyframes heart-beat {
  0% { transform: scale(1); }
  15% { transform: scale(1.2); }
  30% { transform: scale(0.95); }
  45% { transform: scale(1.1); }
  60% { transform: scale(1); }
}

.is-issue-icon.is-issue-reaction {
  transition: all 0.3s ease;
}

.is-icon-reaction-wrap {
  transition: background 0.3s ease, box-shadow 0.3s ease;
  position: relative;
}

.is-issue-like-animation .is-icon-reaction-wrap {
  animation: heart-beat 0.8s ease;
}

.heart-particle {
  will-change: transform, opacity;
  position: absolute;
  pointer-events: none;
  z-index: 10000;
}

.heart-particles-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 9999;
}